const {OnUnmounted} = require("./on-unmounted");
const {Static} = require("./static");
const {Invoke} = require("./invoke");
const {fragments} = require("./fragments");
const {UseState} = require("./use-state");
const {cs} = require("./chain-services");

const ActiveEvents = ({events, next}) => cs(
    ["state", (_, next) => UseState({next})],
    ({state}) => fragments(
        next({
            ref: (dom) => dom && !state.value && state.onChange(dom),
        }),

        state.value && cs(
            ["onRemoveds", (_, next) => Static({initValue: [], next})],
            ({onRemoveds}) => fragments(
                Invoke({
                    props: {events},
                    fn: ({getLatestProps}) => {
                        const dom = state.value;

                        const list = [
                            {
                                reactName: "onTouchStart",
                                domName: "touchstart",
                            },
                            {
                                reactName: "onTouchEnd",
                                domName: "touchend",
                            },
                            {
                                reactName: "onTouchMove",
                                domName: "touchmove",
                            },
                        ];

                        list.forEach(({reactName, domName}) => {
                            if (!events[reactName]) {
                                return;
                            }
                            const listener = (e) => {
                                const {events} = getLatestProps();
                                events[reactName]?.(e);
                            };
                            dom.addEventListener(domName, listener);
                            onRemoveds.push(() => {
                                dom.removeEventListener(domName, listener);
                            })
                        });

                    },
                }),
                OnUnmounted({
                    action: () => onRemoveds.forEach((or) => or()),
                })
            ),
        ),
    ),
);
exports.ActiveEvents = ActiveEvents;
