
const genInt = (from, to, until) => {
    for (let i = 0; i < 1000; i++) {
        const i1 = genInt1(from, to);
        if (until == null || until(i1)) {
            return i1;
        }
    }
};
exports.genInt = genInt;

const genInt1 = (from, to) => Math.floor(Math.random() * (to-from)) + from;
