import * as React from "react";
import {cs} from "../../../../../../common/react/chain-services";
import {UseState} from "../../../../../../common/react/use-state";
import "./random-word.scss";
import {parseUrlQuery} from "../../../../../../common/browser/parse-url-query";
import {selectRandom} from "../../../../../../common/utils/random";

export const RandomWord = ({location}) => cs(
    ["params", (_, next) => next(parseUrlQuery(location.search)??{})],
    ["words", ({params}, next) => next((params["s"] ?? "once upon a time there was a boy called").split(/\W+/))],
    ["state", ({words}, next) => UseState({
        getInitValue: () => selectNextWord(null, words),
        next,
    })],
    ({state, words}) => (
        <div
            className="random-word-01w"
            onClick={() => state.change((s) => selectNextWord(s, words))}
        >
            <div className="box">
                {state.value}
            </div>
        </div>
    ),
);

const selectNextWord = (oldWord, words) => {
    return repeatUntil(
        () => {
            const word = selectRandom(words);
            if (word !== oldWord) {
                return word;
            }
        }
    );
}

const repeatUntil = (fn, count = 100) => {

    for (let i = 0; i < count; i++) {
        const ret = fn();
        if (ret) {
            return ret;
        }
    }
};