import {Load} from "../../../../../../common/react/load";
import {createArray} from "../../../../../../common/utils/collections";
import {cs} from "../../../../../../common/react/chain-services";
import * as React from "react";
import {LsState} from "../../../../../../common/react/ls-state";
import "../common/main.scss";
import {Layout} from "../../common/layout/layout";
import {genNumbers} from "../common/gen-numbers";
import {genInt} from "../common/generators/common/gen-int";
import {formatEquation} from "../common/format-equation";

export const MathMinus = ({}) => cs(
    ({}, next) => Layout({next}),
    ["config", (_, next) => LsState({
        key: "math-minus.config",
        defaultValue: {
            max: 20,
        },
        next,
    })],

    ["result", ({config}, next) => Load({
        _key: JSON.stringify(config.value),
        fetch: () => generate(config.value),
        next,
    })],
    ({result}) => result && (
        <div className="math-main-1">
            <div className="">
                Trừ dưới 20
            </div>
            <div className="">
                {result.map((eq, i) => (
                    <div key={i}>
                        {formatEquation(eq)}
                    </div>
                ))}
            </div>
        </div>
    )
);

const generate = ({max}) => {
    return createArray(20).map(() => genNumbers([
        () => genInt(2, max),
        (prev) => genInt(1, max, (n) => n < prev[0])
    ]));
};
