const {conflict} = require("./conflict");

const genWordLocations = ({width, height, lengths}) => {
    if (lengths.findIndex((length) => length > width && length > height) > -1) {
        return null;
    }

    let locations = [];

    for (let i = 0; i < lengths.length; i++) {
        const length = lengths[i];

        const loc = genLoc({width, height, length, preLocs: locations});
        if (!loc) {
            return null;
        }
        locations.push(loc);
    }
    // console.log(locations)

    return locations;
};
exports.genWordLocations = genWordLocations;

const genLoc = ({preLocs, width, height, length}) => {
    for (let i = 0; i < 100; i++) {
        const loc = (() => {
            const direction = Math.random() < .7 ? "horizontal" : "vertical";
            return {
                start: direction === "horizontal" ? {
                    x: Math.floor(Math.random() * (width - length)),
                    y: Math.floor(Math.random() * (height)),
                } : {
                    x: Math.floor(Math.random() * (width)),
                    y: Math.floor(Math.random() * (height - length)),
                },
                direction,
                length,
            };
        })();

        if (!preLocs.find((l1) => conflict(l1, loc))) {
            return loc;
        }
    }
};
