
const applyWords = (matrix, wordLocations) => {
    matrix = matrix.map((row) => row.slice(0));

    for (const {word, location: {start, direction, length}} of wordLocations) {
        for (let i = 0; i < length; i++) {
            const {x, y} = direction === "horizontal" ? {
                x: start.x + i,
                y: start.y,
            } : {
                x: start.x,
                y: start.y + i,
            };
            matrix[y][x] = word[i];
        }
    }
    return matrix;
};
exports.applyWords = applyWords;
