import {cs} from "../../../../common/react/chain-services";
import {Route, Router, Switch} from "react-router";
import {redirect} from "./common/redirect";
import {routerHistory} from "./common/router-history";
import * as React from "react";
import {IndexRoute} from "../routes/index/index-route";
import {routes} from "./routes";

export const MaxApp = () => cs(
    ({}) => {
        const defaultRoute = "/index";
        return (
            <Router history={routerHistory}>
                <Switch>
                    {[{ path: "/index",              component: IndexRoute  }, ...routes].map(({path, component}, i) => (
                        <Route
                            key={i}
                            exact
                            path={path}
                            component={component}
                        />
                    ))}

                    <Route render={redirect(defaultRoute)}/>
                </Switch>
            </Router>
        );
    }
)
