import {cs} from "../../../../../../common/react/chain-services";
import * as React from "react";
import {UseState} from "../../../../../../common/react/use-state";
import {bindInput} from "../../../../../../common/react/bind-input";
import {Invoke} from "../../../../../../common/react/invoke";
import {waitTimeout} from "../../../../../../common/utils/wait-timeout";
import "./typing-1.scss";
import {keyed} from "../../../../../../common/react/keyed";
import {Sample} from "./sample/sample";
import {scope} from "../../../../../../common/react/scope";

export const Typing1 = ({}) => cs(
    ["state", (_, next) => UseState({next})],
    ["sample", (_, next) => next(sample.trim().replace(/\s{2,}/g, " "))],
    ({state, sample}) => (
        <div className="typing-1-1ws">
            <div className="main">
                <div className="title">
                    The typing game
                </div>

                {Sample({sample, value: state.value?.typing})}

                <textarea {...{
                    ...bindInput(scope(state, ["typing"])),
                    placeholder: "Please type in the paragraph above...",
                }}/>

                {state.value?.typing && state.value?.typing !== sample.substring(0, state.value?.typing.length) && cs(
                    keyed(state.value.typing.length),
                    () => PlaySound({
                        src: require("./buzz.wav"),
                        length: .5,
                    }),
                )}

            </div>
        </div>
    ),
);

const sample = `
    In the beginning Jack sold his cow for some magic beans.
    Next the magic beans grew into a beanstalk and Jack climbed it.
    Then, at the top of the beanstalk he found a giant living in the castle.
    After that, he tried to take the harp and a hen that lays golden eggs.
    The giant started chasing him back down the beanstalk.
    Finally, Jack called for help and his mom came to cut down the beanstalk with an axe.
    In the end, the giant fell off of the beanstalk and the family lived happily ever after.
`;

const PlaySound = ({src, length}) => Invoke({
    fn: async () => {
        const snd = new Audio(src);
        snd.play();
        await waitTimeout(length * 1000);
        snd.pause();
    },
});
