const {createArray} = require("../../../../../../../../common/utils/collections");
const {findWord} = require("../words/find-word");

const genMatrixAvoidWords = ({width, height, wordList}) => {
    const gen1 = () => createArray(height).map(() => (
        // "called".split("")
        createArray(width).map(() => (
            String.fromCharCode("a".charCodeAt(0) + Math.floor(Math.random() * 26))
            // "x"
        ))
    ));

    for (let i = 0; i < 100; i++) {
        const matrix = gen1();
        if (wordList.find((word) => findWord(word, matrix))) {
            continue;
        }
        return matrix;
    }
};
exports.genMatrixAvoidWords = genMatrixAvoidWords;

