const {inRange} = require("../../../../../../../../common/utils/ranges");
const {toRange, conflict: rangeConflict} = require("../../../../../../../../common/utils/ranges");

const conflict = (l1, l2) => {
    if (l1.direction === l2.direction) {
        if (l1.direction === "horizontal") {
            if (l1.start.y !== l2.start.y) {
                return false;
            } else {
                return rangeConflict(
                    toRange(l1.start.x, l1.length),
                    toRange(l2.start.x, l2.length),
                );
            }
        } else {
            if (l1.start.x !== l2.start.x) {
                return false;
            } else {
                return rangeConflict(
                    toRange(l1.start.y, l1.length),
                    toRange(l2.start.y, l2.length),
                );
            }
        }
    } else {
        if (l1.direction === "horizontal") {
            return crossConflict({
                h: l1,
                v: l2,
            });
        } else {
            return crossConflict({
                h: l2,
                v: l1,
            });
        }
    }
};
exports.conflict = conflict;

const crossConflict = ({h, v}) => {
    if (!inRange(v.start.x, toRange(h.start.x, h.length))) {
        return false;
    }
    if (!inRange(h.start.y, toRange(v.start.y, v.length))) {
        return false;
    }
    return true;
}
