const readWord = ({matrix, d: {start, direction, length}}) => {
    let s = "";
    for (let i = 0; i < length; i++) {
        const {x,y} = direction === "horizontal" ? {
            x: start.x + i,
            y: start.y,
        } : {
            x: start.x,
            y: start.y + i,
        };
        if (x > matrix[0].length - 1) {
            return null;
        }
        if (y > matrix.length - 1) {
            return null;
        }
        s += matrix[y][x];
    }
    return s;
};
exports.readWord = readWord;
