const {genNumbers} = require("./gen-numbers");

const toMath1 = (deltas) => {
    let s = deltas[0];

    for (let i = 1; i < deltas.length; i++) {
        const delta = deltas[i];
        s += delta >= 0 ? " + " + delta : " - " + -delta;
    }
    return s;
};

const toDeltas = (numbers) => {
    let v = numbers[0];
    let ret = [v];
    for (let i = 1; i < numbers.length; i++) {
        const number = numbers[i];
        const delta = number - v;
        v = number;

        ret.push(delta);
    }
    return ret;
};
const formatEquation = (numbers) => {
    return toMath1(toDeltas(numbers));
};
exports.formatEquation = formatEquation;