import {Load} from "../../../../../../common/react/load";
import {createArray} from "../../../../../../common/utils/collections";
import {cs} from "../../../../../../common/react/chain-services";
import * as React from "react";
import {LsState} from "../../../../../../common/react/ls-state";
import "../common/main.scss";
import {Layout} from "../../common/layout/layout";
import {genNumbers} from "../common/gen-numbers";
import {genInt} from "../common/generators/common/gen-int";
import {formatEquation} from "../common/format-equation";
import {bindInputNumber} from "../../../../../../common/react/bind-input-number";
import {scope} from "../../../../../../common/react/scope";
import {IgnoreUpdate} from "../../../../../../common/react/ignore-update";
import {UseState} from "../../../../../../common/react/use-state";

const defaultConfig = {
    firstMin: 50,
    max: 110,
    numCount: 3,
    eqCount: 10,
};

export const MathPlusMinus110 = ({}) => cs(
    ({}, next) => Layout({next}),
    ["config", (_, next) => LsState({
        key: "math-plus-minus-110.config",
        defaultValue: defaultConfig,
        next: ({value, ...props}) => next({
            value: {...defaultConfig, ...value},
            ...props,
        }),
    })],

    ["state", (_, next) => UseState({next})],

    ["result", ({config, state}, next) => Load({
        _key: JSON.stringify([config.value, state.value]),
        fetch: () => generate(config.value),
        next,
    })],
    ({result}, next) => IgnoreUpdate({
        when: () => !result,
        next
    }),
    ({result, config, state}) => result && (
        <div className="math-main-1">
            <div className="">
                Cộng trừ dưới {config.value.max}
            </div>
            <div className="">
                {result.map((eq, i) => (
                    <div key={i}>
                        {i+1}. {formatEquation(eq)} = ?
                    </div>
                ))}
            </div>

            {cs(
                ["expanded", (_, next) => UseState({next})],
                ({expanded}) => (
                    <div className="config">
                        <div className="">
                            <a onClick={() => state.change(v=>(v??0)+1)}>More</a>
                        </div>
                        <div className="">
                            <a onClick={() => expanded.change(v=>!v)}>Config</a>
                        </div>

                        {expanded.value && [
                            {
                                label: "First min",
                                path: ["firstMin"],
                            },
                            {
                                label: "Max",
                                path: ["max"],
                            },
                            {
                                label: "Num Count",
                                path: ["numCount"],
                            },
                            {
                                label: "Equation Count",
                                path: ["eqCount"],
                            },
                        ].map(({label, path}, i) => (
                            <div className="" key={i}>
                                {label}:
                                <input {...{
                                    ...bindInputNumber(scope(config, path))
                                }} />
                            </div>
                        ))}
                    </div>
                )
            )}
        </div>
    )
);

const generate = ({firstMin, max, numCount, eqCount}) => {
    return createArray(eqCount).map(() => genNumbers(
        createArray(numCount).map(() =>
            (prev) => genInt(prev.length === 0 ? firstMin : 0, max, (n) => prev.findIndex((n1) => n1 === n) === -1),
        )
    ));
};
