import "./sample.scss";
import * as React from "react";
import {chain} from "../../../../../../../common/utils/fs";

export const Sample = ({sample, value}) => (
    <div className="sample sample-45s">
        {chain(
            compare({sample, value}),
            ({common, error, remain}) => <>
                <div className="common">
                    {common}
                </div>
                <div className="error">
                    {error}
                </div>
                <div className="remain">
                    {remain}
                </div>
            </>
        )}
    </div>
);

const compare = ({sample, value}) => {
    value = value ?? "";
    // if (!value) {
    //     return {
    //         remain: sample,
    //     };
    // }
    for (let i = value.length; i > -1; i--) {
        const v = value.substring(0, i);
        if (v === sample.substring(0, i)) {
            return {
                common: v,
                error: sample.substring(i, value.length),
                remain: sample.substring(value.length),
            }
        }
    }
};