import {cs} from "../../../../../../../../common/react/chain-services";
import {cx} from "emotion";
import * as React from "react";
import {keyed} from "../../../../../../../../common/react/keyed";
import "./word-list.scss";
import {ActiveEvents} from "../../../../../../../../common/react/active-events";

export const WordList = ({wordList, isSelected, onHold, onRelease}) => (
    <div className="word-list word-list-9yf">
        {wordList.map(({word, disabled}, i) => cs(
            keyed(i),
            ["ae", (_, next) => ActiveEvents({
                events: {
                    onTouchStart: (e) => {
                        e.preventDefault();
                        onHold(word);
                    },
                },
                next,
            })],
            ({ae}) => (
                <div
                    ref={ae.ref}
                    className={cx("word", {selected: isSelected(word), disabled})}
                    onMouseDown={(e) => {
                        e.preventDefault();
                        onHold(word);
                    }}
                    onMouseUp={() => onRelease()}
                    onMouseLeave={() => onRelease()}
                    onTouchEnd={() => onRelease(word)}
                >
                    {word}
                </div>
            )
        ))}
    </div>
);


