const {applyWords} = require("./apply-words");
const {chain} = require("../../../../../../../../common/utils/fs");
const {genWordLocations} = require("./gen-word-locations");
const {genMatrixAvoidWords} = require("./gen-matrix-avoid-words");

const generateMatrix = ({size: {width, height}, wordList}) => {
    const wordLocations = genWordLocations({width, height, lengths: wordList.map((w) => w.length)})
    if (!wordLocations) {
        return null;
    }

    const matrix = genMatrixAvoidWords({width, height, wordList});
    if (!matrix) {
        console.log("Cannot avoid words");
        return null;
    }
    return chain(
        matrix,
        (_) => applyWords(_, wordList.map((word, i) => ({word, location: wordLocations[i]})))
    );

};
exports.generateMatrix = generateMatrix;


