import {cs} from "../../../../../../common/react/chain-services";
import * as React from "react";
import {Game} from "./game/game";
import {parseUrlQuery} from "../../../../../../common/browser/parse-url-query";

export const WordSearchRoute = ({location}) => cs(
    () => {
        const {
            s = "once upon a time there was a boy called Jack",
            size = 7,
        } = parseUrlQuery(location.search) ?? {};
        return (
            <div className="">
                {Game({
                    size: +size,
                    sentence: s,
                })}
            </div>
        );
    }
);

