const {readWord} = require("./read-word");
const findWord = (word, matrix) => {
    for (let y = 0; y < matrix.length; y++) {
        for (let x = 0; x < matrix[0].length; x++) {
            let d = horizontal(word.length, {x, y});
            if (word === readWord({d, matrix})) {
                return d;
            }
            d = vertical(word.length, {x, y});
            if (word === readWord({d, matrix})) {
                return d;
            }
        }
    }
};
exports.findWord = findWord;

const horizontal = (length, start) => ({
    start,
    direction: "horizontal",
    length,
});
const vertical = (length, start) => ({
    start,
    direction: "vertical",
    length,
});