import {cs} from "../../../../../../../../common/react/chain-services";
import * as React from "react";
import "./grid.scss";
import {cx} from "emotion";
import {keyed} from "../../../../../../../../common/react/keyed";
import {ActiveEvents} from "../../../../../../../../common/react/active-events";
import {GlobalMouseUp} from "../../../../../../../../common/react/keys/global-mouse-up";

export const Grid = ({matrix, onMouseDown, onMouseEnter, onTouchEnd, getColor, markedLocations}) => cs(
    () => (
        <div className="grid grid-3ow">
            <table >
                <tbody>
                {matrix?.map((row, y) => (
                    <tr className="" key={y}>
                        {row.map((cell, x) => cs(
                            keyed(x),
                            ["ae", (_, next) => ActiveEvents({
                                events: {
                                    onTouchStart: (e) => {
                                        if (e.touches.length === 1) {
                                            e.preventDefault();
                                            onMouseDown?.({x,y});
                                        }
                                    },
                                    onTouchEnd: () => {
                                        onTouchEnd?.();
                                    },
                                    onTouchMove: (e) => {
                                        e.preventDefault();
                                        const rootRect = closest(e.target, (e) => e.nodeName === "TABLE").getBoundingClientRect();
                                        const touch = e.touches[0];

                                        onMouseEnter?.({
                                            x: Math.floor((touch.clientX - rootRect.left) / rootRect.width * matrix[0].length),
                                            y: Math.floor((touch.clientY - rootRect.top) / rootRect.height * matrix.length),
                                        });
                                    },

                                },
                                next,
                            })],
                            ({ae}) => (
                                <td {...{
                                    onMouseDown : () => onMouseDown?.({x,y}),
                                    onMouseEnter: () => onMouseEnter?.({x,y}),
                                    ref: ae.ref,
                                    className: cx(getColor({x,y}, cell)),
                                }}>
                                    {cell}
                                </td>
                            ),
                        ))}
                    </tr>
                ))}
                </tbody>
            </table>

            <div className="markedLocations">
                {markedLocations?.map((location, i) => (
                    <div {...{
                        key: i,
                        className: cx("location", location.direction),

                        ...location.direction === "horizontal" ? {
                            style: {
                                top : location.start.y * 40 + 5,
                                left: location.start.x * 40 + 5,
                                width: location.length * 40 - 5*2,
                            },
                        }: {
                            style: {
                                top : location.start.y * 40 + 5,
                                left: location.start.x * 40 + 5,
                                height: location.length * 40 - 5*2,
                            },
                        },
                    }}/>
                ))}
            </div>


            {onTouchEnd && <>
                {GlobalMouseUp({
                    action: onTouchEnd,
                })}
            </>}
        </div>
    )
);


const closest = (node, check) => {
    if (check(node)) {
        return node;
    }
    const parentNode = node.parentNode;
    if (!parentNode) {
        return null;
    }
    return closest(parentNode, check);
};